import React, { Component } from 'react';
import '../../App.css';
import { Card, Badge } from 'antd';
import { Link } from 'react-router-dom';
import Message from '../../ABI/message';

class mainCardShentai extends Component {
  constructor(props) {
    super(props);
    this.state = {
      LGDEX: '',
      Bip39: '',
      msgCount: '0',
      inboxCount: '',
      ChainID: ''
    }
  }

  componentWillReceiveProps(currentPoprs) {
    this.setState({
      ChainID: currentPoprs.ChainID
    })
  }

  async componentDidMount() {
    //LGDEX
    const url = await window.location.host;
    this.setState({ LGDEX: 'https://' + url + '/LGDEX/index.html' });
    this.setState({ Bip39: 'https://' + url + '/BIP39/index.html' });
    const inboxCount = await Message.methods.inboxCount().call({ from: window.defaultAccount });
    if (localStorage['isRead' + window.defaultAccount] !== undefined) {
      var _isRead = localStorage['isRead' + window.defaultAccount].split(',');
      this.setState({ msgCount: inboxCount - _isRead.length + 1 })
    } else {
      this.setState({ msgCount: inboxCount })
    }
    this.setState({ ChainID: this.props.ChainID })
  }

  ChangChainId = async (name) => {
    this.props.changChainId(name)
  }

  chackChainId = async (_id, name, to) => {
    if (window.ChainID !== _id) {
      await this.ChangChainId(name)
    } else {
      window.location.href = to
    }

  }


  render() {
    return (
      <div>
        <Card type="inner" title="" size='small' style={{ borderRadius: '10px' }}>
          <div>
            <div >
              <div style={{ width: '25%', float: 'left' }}>
                <div style={{ textAlign: 'center' }}>
                  <Link to="/STAirdrop">
                    <div className='kongtou' />
                  </Link>
                  <div></div>
                </div>
              </div>
              <div style={{ width: '25%', float: 'left' }}>
                <div style={{ textAlign: 'center' }}>
                  <div onClick={() => { this.chackChainId('0x80', 'HECO', '/#/hongbao') }}>
                    <div className='hongbao' />
                  </div>
                  <div></div>
                </div>
              </div>
              <div style={{ width: '25%', float: 'left' }}>
                <div style={{ textAlign: 'center' }}>
                  <a href={this.state.LGDEX}>
                    <div className='LGDEX' />
                  </a>
                  <div >LG</div>
                </div>
              </div>
              <div style={{ width: '25%', float: 'left' }}>
                <div style={{ textAlign: 'center' }}>
                  <div onClick={() => { this.chackChainId('0x80', 'HECO', '/#/messageHome') }}>
                    <div className='message' >
                      <div style={{ float: 'right' }} >
                        <Badge count={this.state.msgCount} size='default' />
                      </div>
                    </div>
                  </div>
                  <div></div>
                </div>
              </div>
              <div style={{ clear: 'both' }}></div>
            </div>
            <div style={{ marginTop: '15px' }}>
              <div style={{ width: '25%', float: 'left' }}>
                <div style={{ textAlign: 'center' }}>
                  <Link to="/TkFactoryHome">
                    <div className='tokenFactory' />
                  </Link>
                  <div></div>
                </div>
              </div>
              <div style={{ width: '25%', float: 'left' }}>
                <div style={{ textAlign: 'center' }}>
                  <Link to="/LockIndex">
                    <div className='lock' />
                  </Link>
                  <div></div>
                </div>
              </div>
              <div style={{ width: '25%', float: 'left' }}>
                <div style={{ textAlign: 'center' }}>
                  <a href={this.state.Bip39}>
                    <div className='bip39' />
                  </a>
                  <div></div>
                </div>
              </div>
              <div style={{ clear: 'both' }}></div>
            </div>
          </div>
        </Card>
      </div>
    );
  }
}

export default mainCardShentai;
