import React, { Component } from 'react';
import '../App.css';
import MainCardShentai from './main/mainCardShentai'
import LGairdrop from "./main/LGairdrop";
import { NotificationTwoTone } from '@ant-design/icons';
import { Link } from 'react-router-dom';
import { Card, Input, Button, Menu, Dropdown } from 'antd';
import { Group } from 'antd/lib/avatar';
import { DownOutlined } from '@ant-design/icons';
import LGC from "../ABI/LGC";

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      ChainID: '',
      user: "",
      oringUser: "",
      chainWarning: "",
      disabled: '',
      chainName: '',
    }
  }
  async componentDidMount() {
    if (typeof window.ethereum !== 'undefined') {
      const ethereum = window.ethereum
      ethereum.autoRefreshOnNetwoorkChange = false
      try {
        const accounts = await ethereum.enable()
        window.defaultAccount = accounts[0].toLowerCase()
        window.user = accounts[0].toLowerCase()
        ethereum.on('accountsChanged', function (accounts) {
        })
        ethereum.on(function (networkVersion) {
        })
      } catch (e) { }
    } else { console.log('metamask') }

    const chainId =await window.ethereum.chainId
    this.setState({ ChainID: chainId })
    window.ChainID = this.state.ChainID
    //ID
    if (chainId === '0x80') { this.setState({ chainName: "", chainWarning: '' }) };
    if (chainId === '0x38') { this.setState({ chainName: "", chainWarning: '!!' }) };
    if (chainId === '0x42') { this.setState({ chainName: "", chainWarning: '!!' }) };

    //
    console.log(window.defaultAccount)
    const _user = window.defaultAccount;
    var userLength = _user.length;
    console.log(userLength)
    var user = _user.substring(0, 6) + '' + _user.substring(userLength - 4);
    this.setState({
      user,
      oringUser: _user
    });

    //LGC
    const LGCbalance = await LGC.methods.balanceOf(window.defaultAccount).call();
    this.setState({ LGCbalance: LGCbalance });
    window.userLGC = LGCbalance;
  }

  ChainID = async (id) => {
    if (id === 'HECO') {
      await window.ethereum.request({
        method: 'wallet_addEthereumChain',
        params: [
          {
            chainId: '0x80',
            chainName: 'HECO',
            nativeCurrency: {
              name: 'HT',
              symbol: 'HT',
              decimals: 18,
            },
            rpcUrls: ['https://http-mainnet.hecochain.com'],
            blockExplorerUrls: ['https://hecoinfo.com/'],
          },
        ],
      })
        .then(() => {
          this.setState({ chainName: "", chainWarning: '', ChainID: '0x80' })
        })
        .catch((e) => {
        })
    }
    if (id === 'BSC') {
      await window.ethereum.request({
        method: 'wallet_addEthereumChain',
        params: [
          {
            chainId: '0x38',
            chainName: 'BSC',
            nativeCurrency: {
              name: 'BNB',
              symbol: 'BNB',
              decimals: 18,
            },
            rpcUrls: ['https://bsc-dataseed.binance.org/'],
            blockExplorerUrls: ['https://bscscan.com'],
          },
        ],
      })
        .then(() => {
          this.setState({ chainName: "", chainWarning: '!!', ChainID: '0x38' })
        })
        .catch((e) => {
        })
    }
    if (id === 'OEC') {
      await window.ethereum.request({
        method: 'wallet_addEthereumChain',
        params: [
          {
            chainId: '0x42',
            chainName: 'OEC',
            nativeCurrency: {
              name: 'OKT',
              symbol: 'OKT',
              decimals: 18,
            },
            rpcUrls: ['https://exchainrpc.okex.org'],
            blockExplorerUrls: ['https://www.oklink.com/okexchain'],
          },
        ],
      })
        .then(() => {
          this.setState({ chainName: "", chainWarning: '!!', ChainID: '0x42' })
        })
        .catch((e) => {
        })
    }
    window.ChainID = this.state.ChainID
  }

  changUser = () => {
    this.setState({ user: this.state.oringUser })
  }

  render() {
    const chain = (
      <Menu >
        <Menu.Item key="1" onClick={() => { this.ChainID('HECO') }}>
          
        </Menu.Item>
        <Menu.Item key="2" onClick={() => { this.ChainID('BSC') }}>
          
        </Menu.Item>
        <Menu.Item key="3" onClick={() => { this.ChainID('OEC') }}>
          
        </Menu.Item>
      </Menu>
    );

    return (

      <div >
        <div>
          <span style={{ color: 'red' }}>
            <NotificationTwoTone />&nbsp;&nbsp;<Link to='/Genxin' style={{ color: 'red' }}
            >
            </Link>
          </span>
        </div>
        <Card type="inner" size='small' style={{ borderRadius: '10px' }}
          bodyStyle={{ paddingTop: '8px', paddingBottom: '8px' }}>
          <div>
            <div>
              <div style={{ background: "yellow", color: "red", fontSize: "14px" }}>
                {this.state.chainWarning}
              </div>
            </div>
            <Group>
              <Dropdown overlay={chain} trigger={['click']}>
                <Button>
                  {this.state.chainName} <DownOutlined />
                </Button>
              </Dropdown>
              <div onClick={() => { this.changUser() }} style={{ marginBottom: '5px' }}>
                <Input id='i1' value={this.state.user} disabled />
              </div>
              <Button>
                <Link to='/LGCtransfer'>
                  <div to='/LGCtransfer' className="gongxian">LGC:{this.state.LGCbalance}</div>
                </Link>
              </Button>
            </Group>
          </div>
        </Card>
        <MainCardShentai ChainID={this.state.ChainID} changChainId={this.ChainID.bind(this)}/>
        <LGairdrop ChainID={this.state.ChainID} />
      </div>
    );
  }
}

export default Home;
